import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


// import HelloWorld from '@/components/HelloWorld'
// import Demo from '@/views/Demo'


// const _import_ = file => () => import('views/' + file + '.vue')


const Layout = r => require.ensure([], () => r(require('@/views/Layout/index.vue')), 'Layout');
const Home = r => require.ensure([], () => r(require('@/views/Home/index.vue')), 'Demo');
const HelloWorld = r => require.ensure([], () => r(require('@/components/HelloWorld.vue')), 'HelloWorld');



export const constantRouterMap = [
	{
		path: '/',
		name: '首页',
		component: Layout,
    redirect: '/home',
    children: [
      {
        path: 'home',
        name: '首页',
        component: Home
      }
    ]
	},
  
]

export default new Router({
  routes: constantRouterMap
})



